
import XHeader from 'vux/src/components/x-header/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import DeviceCard from '@/views/DeviceCard'

export default {
name: 'DeviceMaintainDetail',
components: {
XHeader,
Swiper,
SwiperItem,
CheckIcon,
GroupTitle,
XButton,
DeviceCard
},
data () {
return {
demo01_index: 0,
demo01_list: [],
plan_id: 0,
plan: {},
device: null,
canFinish: false
}
},
mounted () {
this.USER = JSON.parse(window.localStorage.getItem('user'))
this.plan_id = this.$route.params.id
if (this.plan_id > 0) {
let url = this.api.maintain + '/' + this.plan_id
this.utils.get(url, this.loadData, this.$vux.confirm)
}
},
methods: {
demo01_onIndexChange (index) {
this.demo01_index = index
},
loadData (response) {
this.plan = response.data
this.device = response.data.device
if (this.device.image_list && this.device.image_list.length > 0) {
for (var i = 0; i < this.device.image_list.length; i++) {
let img = {}
img.url = 'javascript:'
img.img = this.device.image_list[i]
this.demo01_list.push(img)
}
}
if (this.USER && this.USER.id > 0 && this.USER.id === this.plan.user.id && this.plan.status === 1) {
this.canFinish = true
}

},
finishPlan () {
let self = this
if (self.plan_id > 0) {
let token = window.localStorage.getItem('token')
let url = self.api.finishPlan + '?id=' + self.plan_id + '&token=' + token
self.utils.get(url, function (response) {
if (response) {
self.loadData(response)
}
}, self.$vux.confirm)
}
}
}
}







