
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Divider from 'vux/src/components/divider/index.vue'

import Bscroll from 'better-scroll'
import DeviceItem from './DeviceItem'
import api from '@/api'

export default {
components: {
XHeader,
Search,
ViewBox,
DeviceItem,
Checker,
CheckerItem,
Divider
},
mounted () {
this.$nextTick(() => {
this.scroll = new Bscroll(this.$refs.wrapper, {
pullDownRefresh: this.pullDownRefreshObj,
click: true
})
this.onPullingDown()

this.scroll.on('pullingDown', this.onPullingDown)

this.scroll.on('scrollEnd', this.onPullingUp)
})
this.getFilters()

this.$store.commit('setSelected', '详细信息')
},
data () {
return {
allDevices: [],
devices: [],
searchText: '',
isScreenShow: false,
filters: {},
category: '',
categorys: [],
statusList: [],
status: '',
searchCondition: [],
scroll,
pullDownRefreshObj: {
threshold: 50,
stop: 20
},
startY: 0, // 纵轴方向初始化位置
scrollToX: 0,
scrollToY: 0,
scrollToTime: 700,
page: 1,
maxpage: 0,
nomore: false
}
},
methods: {

scrollTo () {
this.scroll.scrollTo(this.scrollToX, this.scrollToY, this.scrollToTime)
},
onPullingDown () {

console.log('下拉刷新')
this.page = 1
let url = this.api.devices
url += '?category=' + this.category
url += '&status=' + this.status
url += '&page=' + this.page
if (this.searchText) {
url += '&keyword=' + this.searchText
}
console.log(url)
this.utils.get(url, this.loadData, this.$vux.confirm)
},
onPullingUp () {

if (!this.nomore && this.scroll.y <= (this.scroll.maxScrollY + 50)) {
this.page++
console.log('上拉加载', this.page)
let url = this.api.devices
url += '?category=' + this.category
url += '&status=' + this.status
url += '&page=' + this.page
if (this.searchText) {
url += '&keyword=' + this.searchText
}
if (this.page <= this.maxpage) {
this.utils.get(url, this.addData, this.$vux.confirm)
} else {
this.nomore = true




}
}
},
addData (response) {

console.log(response)
this.devices = this.devices.concat(response.data)
this.allDevices = this.allDevices.concat(response.data)

if (response.pagination.totalCount > 0 && response.pagination.defaultPageSize) {
this.maxpage = Math.ceil(response.pagination.totalCount / response.pagination.defaultPageSize)
}
console.log(this.maxpage)
this.refreshScroll()
},
loadData (response) {

this.devices = response.data
this.allDevices = response.data
if (response.pagination.totalCount > 0 && response.pagination.defaultPageSize) {
this.maxpage = Math.ceil(response.pagination.totalCount / response.pagination.defaultPageSize)
}
console.log(this.maxpage)
this.refreshScroll()
},
refreshScroll () {
let self = this
this.$nextTick(() => {
if (!self.scroll) {
self.scroll = new Bscroll(self.$refs.wrapper, {
pullDownRefresh: self.pullDownRefreshObj,
click: true
})
} else {
self.scroll.refresh()
}

self.scroll.finishPullDown()
})
},

getFilters () {
let self = this
this.utils.get(this.api.filters, function (response) {
console.log(response)
self.filters = response.data
self.categorys = response.data.categorys.values
self.statusList = response.data.status.values
}, this.$vux.confirm)
},
formatDevice (device) {
return {
title: device.name,
desc: device.description,
src: device.images
}
},
resultClick (item) {
window.alert('you click the result item: ' + JSON.stringify(item))
this.$nextTick(() => {
this.scroll = new Bscroll(this.$refs.wrapper, {
pullDownRefresh: this.pullDownRefreshObj,
click: true
})
this.onPullingDown()

this.scroll.on('pullingDown', this.onPullingDown)

this.scroll.on('scrollEnd', this.onPullingUp)
})
},
getResult (val) {
console.log('on-change', this.searchText)
},
getData () {
const self = this
let url = api.devices
url += '?category=' + self.category
url += '&status=' + self.status
if (self.searchText) {
url += '&keyword=' + self.searchText
}
console.log(url)
this.axios
.get(url)
.then(response => {
console.log(response.data.data)
this.devices = response.data.data
this.allDevices = response.data.data
})
.catch(error => {
console.log(error)
})
.finally()
},
onSubmit () {
console.log('on-submit', this.searchText)
this.$nextTick(() => {
this.scroll = new Bscroll(this.$refs.wrapper, {
pullDownRefresh: this.pullDownRefreshObj,
click: true
})
this.onPullingDown()

this.scroll.on('pullingDown', this.onPullingDown)

this.scroll.on('scrollEnd', this.onPullingUp)
})
},
onFocus () {
console.log('on focus')
},
onCancel () {
console.log('on cancel')
this.devices = this.allDevices
},
clickScreen () {
this.isScreenShow = true

},
clickTime () {

},
endScreen () {

this.isScreenShow = false
this.$nextTick(() => {
this.scroll = new Bscroll(this.$refs.wrapper, {
pullDownRefresh: this.pullDownRefreshObj,
click: true
})
this.onPullingDown()

this.scroll.on('pullingDown', this.onPullingDown)

this.scroll.on('scrollEnd', this.onPullingUp)
})
}
},
watch: {
isScreenShow: function (newVal, oldVal) {
console.log(newVal, 'isScreenShow', oldVal)
if (oldVal) {

}
}
}
}



