
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


export default {
name: 'DeviceCard',
components: {
Flexbox,
FlexboxItem
},
props: {
device: Object
},
data () {
return {
flexspan: 2 / 3,
hasImage: 1
}
},
mounted () {
console.log(this.device.images)
if (this.device.images === '') {
this.hasImage = 0
this.flexspan = 0
console.log(this.flexspan)
}
}
}



