
import Card from 'vux/src/components/card/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import api from '@/api'

const list = () => ['详情信息', '维修记录', '维护记录']

export default {
name: 'device-view',
components: {
Card,
Tab,
TabItem,
Swiper,
SwiperItem,
XHeader,
Flexbox,
FlexboxItem,
DeviceInfo: () => import('./DeviceInfo'),
MaintainList: () => import('./DeviceMaintainList'),
RepairList: () => import('./DeviceRepairList')
},
data () {
return {
index: 0,
index01: 0,
selected: '详细信息',
list: list(),
getBarWidth: function (index) {
return (index + 1) * 22 + 'px'
},
device: null,
errored: false,
loading: false,
fullHeight: (document.documentElement.clientHeight - document.documentElement.clientWidth * 0.4 - 90) / document.documentElement.clientWidth
}
},
created () {


let tab = this.$route.params.tab
if (tab > 0) {
this.$store.commit('setSelected', list()[tab - 1])
this.selected = list()[tab - 1]
} else {
this.selected = this.$store.state.selected
}
},
mounted () {
this.axios
.get(api.detail + this.$route.params.id)
.then(response => {
this.device = response.data.data

})
.catch(error => {
console.log(error)
this.errored = true
})
.finally()
},
watch: {
fullHeight (val) {
if (!this.timer) {
this.fullHeight = val
this.timer = true
let that = this
setTimeout(function () {
that.timer = false
}, 400)
}
}
},
methods: {
switchTabItem (index) {
console.log('on-before-index-change', index)
this.$vux.loading.show({
text: 'loading'
})
setTimeout(() => {
this.$vux.loading.hide()
this.index01 = index
}, 1000)
},
onItemClick (index) {

this.$store.commit('setSelected', list()[index])
},
addTab () {
if (this.list.length < 5) {
this.list = list().slice(0, this.list.length + 1)
}
},
removeTab () {
if (this.list.length > 1) {
this.list = list().slice(0, this.list.length - 1)
}
},
next () {
if (this.index === this.list.length - 1) {
this.index = 0
} else {
++this.index
}
},
prev () {
if (this.index === 0) {
this.index = this.list.length - 1
} else {
--this.index
}
}
}
}



