
import Bscroll from 'better-scroll'
import XHeader from 'vux/src/components/x-header/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'

export default {
components: {
XHeader,
LoadMore,
Tab,
TabItem,
ButtonTab,
ButtonTabItem
},
mounted () {
this.$store.commit('setGoback', 'myrepairs')
this.TOKEN = window.localStorage.getItem('token')
this.USER = JSON.parse(window.localStorage.getItem('user'))
if (!this.USER) {
this.utils.deletaUserData()
} else {

}
this.$nextTick(() => {
this.scroll = new Bscroll(this.$refs.wrapper, {
pullDownRefresh: this.pullDownRefreshObj,
click: true
})
this.onPullingDown()

this.scroll.on('pullingDown', this.onPullingDown)

this.scroll.on('scrollEnd', this.onPullingUp)
})
},
data () {
return {

pullDownRefreshObj: {
threshold: 50,
stop: 20
},
startY: 0, // 纵轴方向初始化位置
scrollToX: 0,
scrollToY: 0,
scrollToTime: 700,
items: [],
scroll,
page: 1,
maxpage: 0,
nomore: false,
loading: false,
defaultImage: 'http://lams-1257122319.cos.ap-beijing.myqcloud.com/20190321eec5375f7c984d813051c65f202ca928',
tabmodel: 0,
statusList: [
{
key: -1,
name: '全部'
},
{
key: 1,
name: '新建'
},
{
key: 3,
name: '已完成'
}
],
defaultStatus: 0,
currentStatus: -1
}
},
methods: {

scrollTo () {
this.scroll.scrollTo(this.scrollToX, this.scrollToY, this.scrollToTime)
},
onPullingDown () {


this.loading = true
this.page = 1
let url = this.api.repairList + '?page=' + this.page
url += '&assignee=' + this.USER.id
if (this.currentStatus >= 0) {
url += '&status=' + this.currentStatus
}
this.utils.get(url, this.loadData, this.$vux.confirm)
},
onPullingUp () {

if (this.scroll.y <= (this.scroll.maxScrollY + 50)) {

this.page ++
if (this.page <= this.maxpage) {
this.loading = true
let url = this.api.repairList + '?page=' + this.page
url += '&assignee=' + this.USER.id
if (this.currentStatus >= 0) {
url += '&status=' + this.currentStatus
}
this.utils.get(url, this.addData, this.$vux.confirm)
} else {
this.nomore = true
}
}
},
addData (response) {


this.items = this.items.concat(response.data)

if (response.pagination.totalCount > 0 && response.pagination.defaultPageSize) {
this.maxpage = Math.ceil(response.pagination.totalCount / response.pagination.defaultPageSize)
}


this.refreshScroll()
},
loadData (response) {
this.items = response.data
if (response.pagination.totalCount > 0 && response.pagination.defaultPageSize) {
this.maxpage = Math.ceil(response.pagination.totalCount / response.pagination.defaultPageSize)
}


this.refreshScroll()
},
refreshScroll () {
let self = this
this.$nextTick(() => {
if (!self.scroll) {
self.scroll = new Bscroll(self.$refs.wrapper, {
pullDownRefresh: self.pullDownRefreshObj,
click: true
})
} else {
self.scroll.refresh()
}

self.scroll.finishPullDown()
self.loading = false
})
},
toMyReports () {

this.$router.push({path: '/myreports'})
},
toMyRepairs () {


},
toMyMaintains () {

this.$router.push({path: '/mymaintains'})
},
changeStatus () {

if (this.defaultStatus === 0) {
this.currentStatus = -1
}
if (this.defaultStatus === 1) {
this.currentStatus = 1
}
if (this.defaultStatus === 2) {
this.currentStatus = 3
}
this.onPullingDown()
}
}
}



