
import XHeader from 'vux/src/components/x-header/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import DeviceCard from '@/views/DeviceCard'
import DeviceInfo from '@/views/DeviceInfo'
import DeviceBar from '@/views/DeviceBar'
import api from '@/api'

export default {
name: 'DevicePage',
components: {
XHeader,
LoadMore,
DeviceCard,
DeviceInfo,
DeviceBar
},
data () {
return {
index: 0,
selected: '详细信息',
device: null
}
},
mounted () {
this.getDevice()
},
methods: {
getDevice () {

let url = api.detail + this.$route.params.id
this.utils.get(url, this.setDevice, this.$vux.confirm)
},
setDevice (response) {
this.device = response.data
}
}
}



