
import Bscroll from 'better-scroll'
import XHeader from 'vux/src/components/x-header/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

let count = 1
export default {
components: {
XHeader,
LoadMore
},
mounted () {
this.$nextTick(() => {
this.scroll = new Bscroll(this.$refs.wrapper, {
pullDownRefresh: this.pullDownRefreshObj,
click: true
})
this.onPullingDown()

this.scroll.on('pullingDown', this.onPullingDown)

this.scroll.on('scrollEnd', this.onPullingUp)
})
},
data () {
return {

scrollbarObj: {
fade: true
},

pullDownRefreshObj: {
threshold: 50,
stop: 20
},

pullUpLoadObj: {
threshold: 0,
txt: {
more: '加载更多',
noMore: '没有更多数据了'
}
},
startY: 0, // 纵轴方向初始化位置
scrollToX: 0,
scrollToY: 0,
scrollToTime: 700,
items: [],
scroll,
page: 1,
maxpage: 0,
nomore: false,
loading: false
}
},
methods: {

scrollTo () {
this.scroll.scrollTo(this.scrollToX, this.scrollToY, this.scrollToTime)
},

getData () {
return new Promise(resolve => {
setTimeout(() => {
const arr = []
for (let i = 0; i < 10; i++) {
arr.push(count++)
}
resolve(arr)
}, 1000)
})
},
onPullingDown () {
this.loading = true

console.log('下拉刷新')
this.page = 1
this.utils.get(this.api.devices + '?page=' + this.page, this.loadData, this.$vux.confirm)
},
onPullingUp () {

if (this.scroll.y <= (this.scroll.maxScrollY + 50)) {
console.log('上拉加载')
this.page ++
if (this.page <= this.maxpage) {
this.loading = true
this.utils.get(this.api.devices + '?page=' + this.page, this.addData, this.$vux.confirm)
} else {
this.nomore = true




}
}
},
addData (response) {

console.log(response)
let self = this
this.items = this.items.concat(response.data)

if (response.pagination.totalCount > 0 && response.pagination.defaultPageSize) {
this.maxpage = Math.ceil(response.pagination.totalCount / response.pagination.defaultPageSize)
}
console.log(this.maxpage)

this.$nextTick(() => {
if (!self.scroll) {
self.scroll = new Bscroll(self.$refs.wrapper, {
pullDownRefresh: self.pullDownRefreshObj,
click: true
})
} else {
self.scroll.refresh()
}

self.scroll.finishPullDown()
self.loading = false
})
},
loadData (response) {

let self = this
this.items = response.data
if (response.pagination.totalCount > 0 && response.pagination.defaultPageSize) {
this.maxpage = Math.ceil(response.pagination.totalCount / response.pagination.defaultPageSize)
}
console.log(this.maxpage)

this.$nextTick(() => {
if (!self.scroll) {
self.scroll = new Bscroll(self.$refs.wrapper, {
pullDownRefresh: self.pullDownRefreshObj,
click: true
})
} else {
self.scroll.refresh()
}

self.scroll.finishPullDown()
self.loading = false
})
}
}
}



