
import Bscroll from 'better-scroll'
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

let count = 1
export default {
components: {
XHeader,
Flexbox,
FlexboxItem
},
mounted () {
this.object_id = this.$route.params.id
this.$nextTick(() => {
this.scroll = new Bscroll(this.$refs.wrapper, {
pullDownRefresh: this.pullDownRefreshObj
})
this.onPullingDown()

this.scroll.on('pullingDown', this.onPullingDown)

this.scroll.on('scrollEnd', this.onPullingUp)
})
},
data () {
return {
object_id: null,
object_type: 1,

scrollbarObj: {
fade: true
},

pullDownRefreshObj: {
threshold: 50,
stop: 20
},

pullUpLoadObj: {
threshold: 0,
txt: {
more: '加载更多',
noMore: '没有更多数据了'
}
},
startY: 0, // 纵轴方向初始化位置
scrollToX: 0,
scrollToY: 0,
scrollToTime: 700,
items: [],
scroll,
page: 1,
maxpage: 0,
nomore: false
}
},
methods: {

scrollTo () {
this.scroll.scrollTo(this.scrollToX, this.scrollToY, this.scrollToTime)
},

getData () {
return new Promise(resolve => {
setTimeout(() => {
const arr = []
for (let i = 0; i < 10; i++) {
arr.push(count++)
}
resolve(arr)
}, 1000)
})
},
onPullingDown () {

console.log('下拉刷新')
this.page = 1
let url = this.api.repairComment + 'page=' + this.page
url += '&object_id=' + this.object_id
url += '&object_type=' + this.object_type
this.utils.get(url, this.loadData, this.$vux.confirm)
},
onPullingUp () {

if (this.scroll.y <= (this.scroll.maxScrollY + 50)) {
console.log('上拉加载')
this.page++
if (this.page <= this.maxpage) {
let url = this.api.repairComment + 'page=' + this.page
url += '&object_id=' + this.object_id
url += '&object_type=' + this.object_type
console.log(this.page)
this.utils.get(url, this.addData, this.$vux.confirm)
} else {
this.nomore = true




}
}
},
addData (response) {

console.log(response)
let self = this
this.items = this.items.concat(response.data)

if (response.pagination.totalCount > 0 && response.pagination.defaultPageSize) {
this.maxpage = Math.ceil(response.pagination.totalCount / response.pagination.defaultPageSize)
}
console.log(this.maxpage)

this.$nextTick(() => {
if (!self.scroll) {
self.scroll = new Bscroll(self.$refs.wrapper, {
pullDownRefresh: self.pullDownRefreshObj
})
} else {
self.scroll.refresh()
}

self.scroll.finishPullDown()
})
},
loadData (response) {

let self = this
this.items = response.data
if (response.pagination.totalCount > 0 && response.pagination.defaultPageSize) {
this.maxpage = Math.ceil(response.pagination.totalCount / response.pagination.defaultPageSize)
}
console.log(this.maxpage)

this.$nextTick(() => {
if (!self.scroll) {
self.scroll = new Bscroll(self.$refs.wrapper, {
pullDownRefresh: self.pullDownRefreshObj
})
} else {
self.scroll.refresh()
}

self.scroll.finishPullDown()
})
}
}
}



