          self.TOKEN = window.localStorage.getItem('token');
          fd.append('token', self.TOKEN);
          fd.append('id', self.repairinfo.id);
          fd.append('type', 1);
          fd.append('score', self.stars);
          var posturl = self.api.score + '?token=' + self.TOKEN;

